import {
	mapGetters
} from "vuex";
import toLogin from "@/libs/login";
import {
	couponReceiveBatch
} from "@/api/user";

export default {
	name: "CouponWindow",
	props: {
		couponList: {
			type: Array,
			default: () => []
		}
	},
	computed: mapGetters(["isLogin"]),
	data: function() {
		return {
			value: true
		};
	},
	mounted: function() {},
	methods: {
		checked() {
			const isLogin = this.isLogin;
			if (!isLogin) return toLogin();

			const ids = this.couponList.reduce((initial, coupon) => {
				initial.push(coupon.id);
				return initial;
			}, []);
			couponReceiveBatch(ids)
				.then(() => {
					this.$emit("success");
					uni.showToast({
						title: '领取成功',
						icon: 'success',
						duration: 2000
					});
				})
				.catch(() => {
					uni.showToast({
						title: '已领取',
						icon: 'none',
						duration: 2000
					});
				});
			if (isLogin) {
				this.value = false;
				this.$emit("checked");
			}
		},
		close: function() {
			this.value = false;
			this.$emit("close");
		}
	}
};
